'use client';

import { use, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { fetchPostBySlug, fetchAllPosts, selectCurrentPost, selectAllPosts } from '../../../lib/redux/blog';
import type { RootState } from '../../../lib/redux/store';
import Link from 'next/link';
import Header from '@/components/header';
import Footer from '@/components/footer';
import BlogDetailsHero from '@/components/Blog/blog-details-hero';
import BlogBreadcrumb from '@/components/Blog/blog-breadcrumb';
import ShareButtons from '@/components/Blog/share-buttons';
import RelatedPosts from '@/components/Blog/related-posts';
import BackToBlog from '@/components/Blog/back-to-blog';
import BlogCategoriesSidebar from '@/components/Blog/blog-categories-sidebar';
import BlogPopularPosts from '@/components/Blog/blog-popular-posts';
import Image from 'next/image';

export default function BlogDetailsPage({ params }: { params: Promise<{ slug: string }> }) {
    const dispatch = useDispatch();
    const currentPost = useSelector(selectCurrentPost);
    const allPosts = useSelector(selectAllPosts);
    const categories = useSelector((state: RootState) => state.blog.categories);
    const loading = useSelector((state: RootState) => state.blog.loading);
    const error = useSelector((state: RootState) => state.blog.error);

    // Unwrap the params Promise
    const { slug } = use(params);

    useEffect(() => {
        // Fetch post by slug from API
        dispatch(fetchPostBySlug(slug) as any);
        // Also fetch all posts for related posts and popular posts
        if (allPosts.length === 0) {
            dispatch(fetchAllPosts() as any);
        }
    }, [slug, dispatch, allPosts.length]);

    // Loading state
    if (loading) {
        return (
            <div className="min-h-screen bg-background">
                <Header />
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 py-20">
                    <div className="text-center max-w-2xl mx-auto">
                        <p className="text-muted-foreground">Loading blog post...</p>
                    </div>
                </div>
                <Footer />
            </div>
        );
    }

    // Error state
    if (error && !currentPost) {
        return (
            <div className="min-h-screen bg-background">
                <Header />
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 py-20">
                    <div className="text-center max-w-2xl mx-auto">
                        <h1 className="text-3xl sm:text-4xl font-bold text-foreground mb-4">Error Loading Post</h1>
                        <p className="text-destructive mb-8">{error}</p>
                        <Link 
                            href="/blog" 
                            className="inline-flex items-center gap-2 text-[#2563eb] hover:text-[#1d4ed8] font-semibold transition-colors"
                        >
                             Back to Blog
                        </Link>
                    </div>
                </div>
                <Footer />
            </div>
        );
    }

    // Not found state
    if (!currentPost) {
        return (
            <div className="min-h-screen bg-background">
                <Header />
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 py-20">
                    <div className="text-center max-w-2xl mx-auto">
                        <h1 className="text-3xl sm:text-4xl font-bold text-foreground mb-4">Blog Post Not Found</h1>
                        <p className="text-muted-foreground mb-8">The blog post you're looking for doesn't exist.</p>
                        <Link 
                            href="/blog" 
                            className="inline-flex items-center gap-2 text-[#2563eb] hover:text-[#1d4ed8] font-semibold transition-colors"
                        >
                             Back to Blog
                        </Link>
                    </div>
                </div>
                <Footer />
            </div>
        );
    }

    // Get related posts (excluding current post)
    const relatedPosts = allPosts
        .filter(post => post.id !== currentPost.id && post.category === currentPost.category)
        .slice(0, 3);

    return (
        <div className="min-h-screen bg-background">
            <Header />
            
            {/* Hero Section */}
            <BlogDetailsHero post={currentPost} />

            {/* Breadcrumb Section */}
            <BlogBreadcrumb post={currentPost} />

            {/* Article Content */}
            <section className="py-12 sm:py-16 md:py-20">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="grid grid-cols-1 lg:grid-cols-12 gap-6 lg:gap-8">
                        {/* Left Side - Content (8 columns) */}
                        <div className="lg:col-span-8">
                            <article className="bg-card rounded-xl p-6 sm:p-8 md:p-10 shadow-sm border border-border">
                            <div
  className="
    prose 
    prose-lg 
    max-w-none
    prose-h1:text-4xl
    prose-h2:text-3xl
    prose-h3:text-2xl
    prose-p:leading-relaxed
    prose-a:text-blue-600
    dark:prose-invert
  "
  dangerouslySetInnerHTML={{ __html: currentPost.content || '' }}
/>


                                {/* Share Section */}
                                <div className="mt-8 pt-8 border-t border-border">
                                    <ShareButtons url={`/blog/${currentPost.slug}`} title={currentPost.title} />
                                </div>
                            </article>
                        </div>

                        {/* Right Side - Sidebar (4 columns) */}
                        <div className="lg:col-span-4">
                            <div className="sticky top-6 space-y-6">
                                {/* Author Card */}
                                <div className="bg-card rounded-xl p-6 shadow-sm border border-border">
                                    <h3 className="text-lg font-bold text-foreground mb-4">Author</h3>
                                    <div className="flex items-center gap-4">
                                        <div className="w-16 h-16 rounded-full bg-gradient-to-br from-[#2563eb] to-[#00D4AA] flex items-center justify-center text-white text-xl font-bold">
                                            {currentPost.author.name.charAt(0).toUpperCase()}
                                        </div>
                                        <div>
                                            <p className="font-semibold text-foreground">{currentPost.author.name}</p>
                                            <p className="text-sm text-muted-foreground">{currentPost.author.bio}</p>
                                        </div>
                                    </div>
                                </div>

                                {/* Tags Card */}
                                {currentPost.tags && currentPost.tags.length > 0 && (
                                    <div className="bg-card rounded-xl p-6 shadow-sm border border-border">
                                        <h3 className="text-lg font-bold text-foreground mb-4">Tags</h3>
                                        <div className="flex flex-wrap gap-2">
                                            {currentPost.tags.map((tag, index) => (
                                                <span
                                                    key={index}
                                                    className="px-3 py-1.5 bg-primary/10 text-primary rounded-full text-sm font-medium border border-primary/20 hover:bg-primary/20 transition-colors"
                                                >
                                                    {tag}
                                                </span>
                                            ))}
                                        </div>
                                    </div>
                                )}

                                {/* Post Details Card */}
                                <div className="bg-card rounded-xl p-6 shadow-sm border border-border">
                                    <h3 className="text-lg font-bold text-foreground mb-4">Post Details</h3>
                                    <div className="space-y-3">
                                        <div className="flex items-center gap-3">
                                            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" className="text-muted-foreground">
                                                <path d="M17 3H3C1.89543 3 1 3.89543 1 5V15C1 16.1046 1.89543 17 3 17H17C18.1046 17 19 16.1046 19 15V5C19 3.89543 18.1046 3 17 3Z" stroke="currentColor" strokeWidth="2" />
                                                <path d="M1 7H19M5 1V5M15 1V5" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
                                            </svg>
                                            <span className="text-sm text-muted-foreground">
                                                {new Date(currentPost.date).toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric' })}
                                            </span>
                                        </div>
                                        <div className="flex items-center gap-3">
                                            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" className="text-muted-foreground">
                                                <path d="M10 18C14.4183 18 18 14.4183 18 10C18 5.58172 14.4183 2 10 2C5.58172 2 2 5.58172 2 10C2 14.4183 5.58172 18 10 18Z" stroke="currentColor" strokeWidth="2" />
                                                <path d="M10 6V10L13 13" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
                                            </svg>
                                            <span className="text-sm text-muted-foreground">{currentPost.readTime}</span>
                                        </div>
                                        <div className="flex items-center gap-3">
                                            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" className="text-muted-foreground">
                                                <path d="M7 3H13C14.1046 3 15 3.89543 15 5V15C15 16.1046 14.1046 17 13 17H7C5.89543 17 5 16.1046 5 15V5C5 3.89543 5.89543 3 7 3Z" stroke="currentColor" strokeWidth="2" />
                                                <path d="M10 7V10M10 13H10.01" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
                                            </svg>
                                            <span className="text-sm text-muted-foreground">Category: <span className="font-semibold text-foreground">{currentPost.category}</span></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            {/* Related Posts */}
            <RelatedPosts posts={relatedPosts} />

            {/* Back to Blog */}
            <BackToBlog />
            
            <Footer />
        </div>
    );
}
